import React, { Component } from "react";
import GeneralInformation from "./GeneralInformation";
import AccountInformation from "./AccountInformation";

import {
  Card,
  CardBody,
  Col,
  Form,
  Row,
  Input,
  CustomInput,
  Label,
  Button,
} from "reactstrap";
import { history } from "../../../../history";
import axiosConfig from "../../../../axiosConfig";

export class EditDriver extends Component {
  constructor(props) {
    super(props);

    this.state = {
      name: "",
      selectedFile: null,
      selectedName: "",
      sortorder: "",
      desc: "",
      product_img: "",
      status: "",
      Id: "",
    };
  }

  onChangeHandler = (event) => {
    this.setState({ selectedFile: event.target.files[0] });
    this.setState({ selectedName: event.target.files[0].name });
    console.log(event.target.files[0]);
  };

  changeHandler1 = (e) => {
    this.setState({ status: e.target.value });
  };
  changeHandler = (e) => {
    this.setState({ [e.target.name]: e.target.value });
  };
  //   async componentDidMount() {
  //     const { id } = this.props.match.params;
  //     console.log(id);
  //     await axiosConfig.get(`/admin/viewone_drive/${id}`).then((response) => {
  //       console.log("viewOneDriver", response.data.data_id);
  //       this.setState({
  //         driverId: response.data.data_id,
  //       });
  //     });
  //   }
  submitHandler = (e) => {
    e.preventDefault();
    const data = new FormData();
    data.append("name", this.state.name);
    data.append("sortorder", this.state.sortorder);
    data.append("desc", this.state.desc);
    data.append("status", this.state.status);
    data.append(
      "product_img",
      this.state.selectedFile,
      this.state.selectedName
    );
    //   for (var value of data.values()) {
    //     console.log(value);
    //  }
    axiosConfig
      .post(" /addproductcategory", data)
      .then((response) => {
        console.log(response);
        this.props.history.push("/app/category/category");
      })
      .catch((error) => {
        console.log(error);
      });
  };
  render() {
    return (
      <div>
        {/* <h1>Update Delivery Man</h1> */}
        <Col sm="12">
          <Col>
            <h1 sm="12">
              <GeneralInformation />
            </h1>
          </Col>
        </Col>
        <Col sm="12">
          <Col>
            <h1 sm="12">
              <AccountInformation />
            </h1>
          </Col>
        </Col>
      </div>
    );
  }
}
export default EditDriver;
